*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background: whitesmoke !important;
}
#body{
  height: 100vh;
  overflow: hidden;
  background: whitesmoke;
  box-shadow: 0 0 4px grey;
}
.header{
  height: fit-content;
  background: whitesmoke;
  z-index: 3;
}
#header{
  border-right: 1px solid #ddd;
}
#main, #header{
  height: 100vh;
  background: whitesmoke;
  overflow-y: scroll;
}
#main::-webkit-scrollbar, #header::-webkit-scrollbar{
  width: 3px;
}
#main::-webkit-scrollbar-thumb, #header::-webkit-scrollbar-thumb{
  background: rgba(0, 0, 0, 0.753);
}
#header h4{
  color: navy;
}
.searchbar_container{
  background: white;
  box-shadow: 0 0 20px rgb(230,230,230);
}
.searchbar_container input{
  outline: none;
  border: none;
  background: none;
}
.searchbar_container button{
  border: none;
  outline: none;
  background: #29236D;
  color: white;
  border-radius: 5px;
  font-size: small;
}
.noti_count{
  background: red;
  width: 15px;
  height: 15px;
  color: white;
  font-size: small;
  border-radius: 50%;
  text-align: center;
}
.online{
  width: fit-content;
  border-radius: 50%;
  background: rgb(0, 255, 0);
  font-family: sans-serif;
  font-size: small;
}
.noti_count, .online{
  position: absolute;
  margin-top: -15px;
  margin-left: 20px;
}


.form {
  display: grid;
}
.pics_cont{
  column-count: 4;
}
/* .card {
  margin-bottom: 1rem;
  display: flex;
} */
.card--image {
  flex: 100%;
  margin-top: 1rem;
  border-radius: 10px;
}

.active{
  font-weight: bold;
  box-shadow: 0 0 3px grey;
}
li{
  list-style: none !important;
  transition: .3s ease-in-out;
}
li:hover{
  font-weight: bold;
  box-shadow: 0 0 3px grey;
  cursor: pointer;
}
ul p{
  font-weight: bold;
  cursor: default;
}
@media (min-width: 768px) {
  .form {
    grid-template-columns: auto 1fr auto;
    grid-gap: 1rem;
    align-items: center;
  }
  .input {
    margin-bottom: 0;
  }
}
@media only screen and (max-width: 600px) {
    .card-list {
        column-count: 1;
    }
}
@media screen and (max-width: 991px) {
  .pics_cont{
    column-count: 3;
  }
}
@media screen and (max-width: 767px) {
  #header{
    position: fixed;
    left: -90%;
    transition: .3s;
  }
  #header.show{
    left: 0;
  }
  .pics_cont{
    column-count: 2;
  }
  @media screen and (max-height: 650px) {
    #header .active{
      margin-top: 40vh !important;
    }
  }
}